:root{
    
    /*Size*/
    --height-header: 12vh;/***/
    --size-menu: 17px;/**/
    --size-title-h1: 130px;/**/
    --size-title-h2: 40px;/**/
    --size-title-h3: 33px;/**/
    --size-paragraph: 16px;/**/
    --line-height: 25px;/**/

    /*Colors*/
    --white: #FFFFFF;
    --black: #11182A;
    --blue: #020D81;
    --gray: #4A5568;
    --gray-alt: #8EA2B9;
    --pink: #FC199D;
    --orange: #FB6B30;
    --sky-blue: #D7EFFC;

    --html: #F4470B;
    --css: #0068BA;
    --js: #E9D44D;
    --bootstrap: #754FAD;
    --sass: #CE6B9E;
    --react: #00D1F7;
    --facebook: #38529A;
    --instagram: #F71E9E;
    --linkedin: #016FAB;
    --github: #000000;
    --twitter: #3993ED;
    --email: #F2463C;

    /*Typography*/
    --font-brand: 'Rubik', sans-serif;
    --font-menu: 'Nunito Sans', sans-serif;
    --font-title: 'Rubik', sans-serif;
    --font-paragraph: 'Nunito Sans', sans-serif;
    --font-alt: 'Space Mono', monospace;
}
.title-h2{
    font-family: 'Rubik', sans-serif;
    font-family: var(--font-title);
    font-size: 40px;
    font-size: var(--size-title-h2);
    letter-spacing: 1px;
}





