.menu-container{
    width: 100%;
    max-width: 1500px;
    height: var(--height-header);
    background-color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    box-shadow: 0 1px 5px rgb(0 0 0 / 10%);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
}
.menu-hamburguesa,
.firm-text{
    display: none;
}
.img-brand{
    width: 210px;
}
.menu-options{
    width: auto;
    height: auto;
}
.menu-links{
    color: var(--gray);
    margin: 0 0 0 45px;
    text-decoration: none;
    font-family: var(--font-alt);
    font-size: var(--size-menu);
    letter-spacing: 0.5px;
}
.menu-links:hover{
    color: var(--pink);
}
.active{
    color: var(--pink);
}
.link-cv{
    padding: 5px 15px;
    margin: 0 0 0 30px;
    background-color: var(--pink);;
    border: 1px solid var(--pink);
    text-decoration: none;
    color: var(--white);
    font-family: var(--font-alt);
    border-radius: 5px;
    font-size: var(--size-menu);
    -webkit-transition: box-shadow 1s, color 1s;
    -moz-transition: box-shadow 1s, color 1s;
    -o-transition: box-shadow 1s, color 1s;
    letter-spacing: 0.5px;
    text-transform: uppercase;
}
.link-cv:hover{
    box-shadow: inset -10.5em 0 0 0 var(--white), inset 10.5em 0 0 0 var(--white);
    color: var(--pink);
}
.icon-download{
    font-size: 13px;
}





/*DONE - OKOKOKOKOK*/
@media screen and (max-width: 900px){
    .menu-container{
        padding: 0 15px 0 10px;
        height: var(--height-header);
        box-shadow: 0 1px 5px rgb(0 0 0 / 20%);
    }
    .menu-hamburguesa{
        width: 40px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
    }
    .icon-menu{
        width: 100%;
        height: 6px;
        background-color: var(--gray);
        position: absolute;
    }
    .icon-menu::before,
    .icon-menu::after{
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        transition: all 0.5s ease-in-out;
        background-color: var(--gray);
    }
    .icon-menu::before{
        top: 12px;
    }
    .icon-menu::after{
        top: -12px;
    }
    .icon-menu-active{
        transition: all 0.5s ease-in-out;
        background-color: var(--gray);
    }
    .icon-menu-active:before,
    .icon-menu-active:after{
        background-color: inherit;
        width: 50%;
        transition: all 0.5s ease-in-out;
    }
    .icon-menu-active:before{
        transform: rotate(30deg);
        top: 5px;
        left: 0.5px;
    }
    .icon-menu-active:after{
        transform: rotate(-30deg);
        top: -5.6px;
        left: 0.5px;
    }

    .img-brand{
        width: 210px;
    }
    .menu-options{
        width: 100vw;
        height: 90vh;
        background-color: hsla(0,0%,100%,.95);
        position: fixed;
        top: var(--height-header);
        left: 0;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        transform: translateY(100%);
        transition: transform 0.5s ease-in-out;
        padding: 80px 30px;
    }
    .menu-active{
        transform: translateY(0%);
    }
    .menu-links{
        margin: 0 0 35px 0;
        font-size: var(--size-menu);
        letter-spacing: 1px;
        font-family: var(--font-title);
        text-transform: uppercase;
        position: relative;
        padding: 3px 0;
        overflow: hidden;
    }
    .menu-links:before{
        content: "";
        width: 100%;
        height: 7%;
        position: absolute;
        bottom: 0;
        left: 0;
        background-color: var(--pink);
        transform: translateX(-100%);
        transition: transform 0.5s ease-in-out;
    }
    .menu-links:hover:before{
        transform: translateX(0%);
    }
    .link-cv{
        padding: 0;
        margin: 0 0 0 0;
        color: #020D81;
        font-family: var(--font-title);
        font-size: var(--size-menu);
        background-color: transparent;
        border: none;
    }
    .link-cv:hover{
        box-shadow: transparent;
        color: var(--pink);
    }
    .icon-download{
        font-size: 25px;
        margin: 0 0 3px 0;
    }
    .firm-text{
        display: block;
        position: fixed;
        right: 3%;
        bottom: 1%;
        font-family: var(--font-alt);
        font-size: 16.5px;
        color: var(--gray-alt);
        z-index: inherit;
    }
}
.welcome-container{
    width: 100%;
    max-width: 1500px;
